.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.5;
}
.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: 2px solid black;
  background-color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  transition: .1s;
  text-transform: uppercase;
}
.contact button:hover{
  color: white;
  background-color: royalblue;
  border-color: royalblue;
}