* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
}

div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  color: #222
}

header {
  flex-basis: 30vh;  
  position: relative;
  overflow: hidden;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0; 
}

aside {
  min-width: 240px;  
}
section.page {
  flex-grow: 1;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 50px;
}
header img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main {
    
    padding: 0 30px 30px 0;
}

.main ul {
    list-style-type: none;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition:.2s;
}
nav.main a:hover {
    transform: translateX(20px);
}
nav.main a.active {
    border-left: 10px gray solid;
    transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
    border-left-color: green;
}
nav.main li:nth-child(2) a.active {
    border-left-color: blue;
}
nav.main li:nth-child(3) a.active {
    border-left-color: orange;
}
nav.main li:nth-child(4) a.active {
    border-left-color: red;
}
.products h2{
  margin-bottom: 20px;

}
.products ul {
  list-style: square;
  padding-left: 30px;

}

.products li {
  margin-bottom: 20px;
}
.products a{
  text-decoration: none;
  color: #333;

}

.products a:hover{
  text-decoration: underline;
  color: black;
}
.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.5;
}
.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: 2px solid black;
  background-color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  transition: .1s;
  text-transform: uppercase;
}
.contact button:hover{
  color: white;
  background-color: royalblue;
  border-color: royalblue;
}
footer span {
  color: red;
  font-size: 14px;
}
